import React from 'react'
import Offers from './Offers'
import { connect } from 'react-redux';
import {removeLI, boolPacked, click_add, onblur} from '../../redux/My_corect/offer'

const OffersContainer = props => {
  const  click = (setT, T) => {
        let texts = T.text.replace(/ +/g, ' ').trim()
       if(texts === '') {
        setT({
            boxShadow: '0px 2px red',
            placeholder: '     ', 
            text:'' 
        })
       } else {
        props.click_add(T.text)
        setT({
            boxShadow: null,
            placeholder: '', 
            text: '' 
        })
       }
   }

    return <Offers 
    arr={props.Todolist} 
    boolPacked={props.boolPacked} 
    removeLI={props.removeLI}
    click={click}
    onblur={props.onblur}
     />

}



function mapStatetoProps(state) {
    return{
        Todolist : state.Todolist
    
      }
    }

    export default connect(mapStatetoProps,{removeLI, boolPacked,click_add, onblur})(OffersContainer)