

const D = {
 ADD_ELEM : 'ADD_ELEM',
 REMOVE_ELEM : 'REMOVE_ELEM'   
}

let counter = 0

const PersonReduser = (state = counter, action) => {
    switch(action.type) {
        case D.ADD_ELEM:
            return state + 1 >= action.length ? 0 : state+1
        case D.REMOVE_ELEM:
            return state -1 < 0 ? action.length-1 : state-1
        default: return state
    }
    
}

export default PersonReduser

export const add_counter = length => ({
    type : D.ADD_ELEM, length
})


export const minus_counter = length => ({
    type : D.REMOVE_ELEM, length
})