import React, { useEffect, useState } from 'react'
import S from './Anim.module.css'
import tree from '../../img/anim/Tree.png'
import './Anim.css'

import Person_1 from '../../img/anim/Person_1.png'
import Person_2 from '../../img/anim/Person_2.png'
import Person_3 from '../../img/anim/Person_3.png'
import Person_4 from '../../img/anim/Person_4.png'
import Person_5 from '../../img/anim/Person_5.png'
import Person_6 from '../../img/anim/Person_6.png'


import ArrowLeft from '../../img/anim/description/ArrowLeft.png'
import ArrowRight from '../../img/anim/description/ArrowRight.png'

import D1 from '../../img/anim/Dragon/D1.png'
import D2 from '../../img/anim/Dragon/D2.png'
import D3 from '../../img/anim/Dragon/D3.png'
import D4 from '../../img/anim/Dragon/D4.png'
import D5 from '../../img/anim/Dragon/D5.png'
import D6 from '../../img/anim/Dragon/D6.png'
import D7 from '../../img/anim/Dragon/D7.png'
import D8 from '../../img/anim/Dragon/D8.png'
import D9 from '../../img/anim/Dragon/D9.png'
import D10 from '../../img/anim/Dragon/D10.png'
import D11 from '../../img/anim/Dragon/D11.png'
import D12 from '../../img/anim/Dragon/D12.png'

import sky from '../../img/anim/sky/sky.png'

import { connect } from 'react-redux'
import { add_counter, minus_counter } from '../../redux/My_corect/Anim/PersonReduser'
import { add_counter_d, minus_counter_d } from '../../redux/My_corect/Anim/DragoneReduser'


    const  mapStatetoProps = state => ({PersonReduser: state.PersonReduser, DragoneReduser: state.DragoneReduser}) 

const w = 30000;
const styletree = {
    width: w + 'px'
}



const AnimContainer = props => {
    return <Anim {...props}/>
}



const Anim = props => {

    const arrPerson = [Person_1,Person_2,Person_3,Person_4,Person_5,Person_6]

    const arrDragone = [D1,D2,D3,D4,D5,D6,D7,D8,D9,D10,D11,D12]

   const [description, setDescription] = useState(true)

   const descriptionElement = description 
   ? <div className={S.descriptio}>
       <div className={S.container_b_d}>
      <button className={S.button_d} onClick={()=> { setDescription(false)}}>X</button>
      </div>
      <h3 className={S.h3_d}>
          Click
      </h3>

      <div className={S.container_Arrow}>
          <img src={ArrowLeft} />  <p>Run to the right</p>

          <img src={ArrowRight} /> <p>Run to the left</p>
      </div>

   </div> 
   : <></>


    let counter_Tree = Math.floor(w/ (10 + window.innerWidth*0.16) )
    const globar_tree_arr = []  
    const global_sky = []  
      
         for(let i = 1; i <= counter_Tree;i++ ){
           globar_tree_arr.push(<img key={'id'+i} onWheel={e=>{anim(e);}} className='tree' src={tree} />)  

           global_sky.push(<img key={'id'+i} onWheel={e=>{anim(e);}} className={'sky'} src={sky} />)           
         }
         



        function anim(e) {
            for (let index = 0; index < globar_tree_arr.length; index++) {
               const animaten = e.srcElement.images[index];
                const animationHeight = animaten?.x;  
                const animaItemOfferset = setXandY(animaten).top;
                let animItemPoint = window.innerHeight - animationHeight 
                if ((window.pageYOffset > animaItemOfferset - animItemPoint) && (window.pageYOffset < animaItemOfferset + animationHeight)) {
                    animaten.classList.add('anim')
                }  else { 
                    animaten  &&  animaten.classList.remove('anim') 
                }

            }
        }
        

            function setXandY(el) {
                const rect = el?.getBoundingClientRect();
                let scrollLeft = window.pageXOffset || document.documentElement?.scrollLeft;
                let scrollTop = window.pageYOffset || document.documentElement?.scrollTop;
                 return {top: rect?.top + scrollTop, left: rect?.left + scrollLeft}
            }

          

    useEffect(()=>{
            window.addEventListener('scroll', (e) => {
                anim(e);
            })
            // window.addEventListener('keydown', e=>{
            //     switch(e.key){
            //         case 'ArrowRight': 
            //         footer_person++
            //         console.log(footer_person);
            //             break;

            //         case'ArrowLeft': 
            //         footer_person--
            //         break;        
            //     }
            // })
    },[])


useEffect(() => {
    const onKeypress = e => {
        switch(e.key){
            case 'ArrowRight': 
            props.add_counter(arrPerson.length)
            props.add_counter_d(arrDragone.length)
                break;
    
            case'ArrowLeft': 
            props.minus_counter(arrPerson.length)
            props.minus_counter_d(arrPerson.length)
            break;        
       }
    }
  
    document.addEventListener('keydown', onKeypress);
  
    return () => {
      document.removeEventListener('keydown', onKeypress);
    };
  }, []);


return(
<div className={S.global}>
<div className={S.top} />


<div className={S.sky_container}>
{global_sky.map(e=> e)}
</div>


{descriptionElement}
<Person 
class={S.person}
footer_person={arrPerson[props.PersonReduser]}
counter={'person'+ props.PersonReduser}
/>

<Person 
class={S.dragone}
footer_person={arrDragone[props.DragoneReduser]}
counter={props.DragoneReduser}
/>

    <div className={S.global_tree} >
 
    {globar_tree_arr.map(e=> e)}

    </div>

<div className={S.bottom} style={styletree}>

    
</div>
</div>
)

}

export default connect(mapStatetoProps, {add_counter,minus_counter,add_counter_d,minus_counter_d})(AnimContainer)

const Person = props =>{
    return(
        <img className={props.class + ' ' + props.counter} onClick={props.NewPlus} src={props.footer_person}/>
    )
}