

const D = {
 ADD_ELEM_D : 'ADD_ELEM_D',
 REMOVE_ELEM_D : 'REMOVE_ELEM_D'   
}

let counter = 0

const DragoneReduser = (state = counter, action) => {
    switch(action.type) {
        case D.ADD_ELEM_D:
            return state + 1 >= action.length ? 0 : state+1
        case D.REMOVE_ELEM_D:
            return state -1 < 0 ? action.length-1 : state-1
        default: return state
    }
    
}

export default DragoneReduser

export const add_counter_d = length => ({
    type : D.ADD_ELEM_D, length
})


export const minus_counter_d = length => ({
    type : D.REMOVE_ELEM_D, length
})