import React, { useState } from "react";
import BodyComponents from "./BodyComponents";
import Footer from "./Components/Footer/Footer";
import TopNavigationContainer from './Components/TopNavigation/TopNavigationContainer'
import './App.css'
import { NavLink, Route, Switch } from "react-router-dom";
import Anim from "./Components/Anim/Anim";
import All from './img/glovalChoice/icons8_profile_100px.png'
import Game from './img/glovalChoice/icons8_game_controller_100px_1.png'

const style_Ap_S = {
 black: { color: 'white',
          backgroundColor: 'rgba(31, 31, 31, 0.993)',
},
  white:{
    color: 'rgb(0, 0, 0)',
    backgroundColor: 'darkgray'
  }
}


const App= props => {
  const [colot_app_f, setColot_app_f] = useState(style_Ap_S.white)

const choice_style = obj =>{
  setColot_app_f(obj)
}


    return (    
       <div style={colot_app_f} className='App' >
        <div>
         <TopNavigationContainer/>     
       </div>
       <div className="lineHeaderMain"></div>
        <div>
         <BodyComponents choice_style={choice_style} />
         <br />
         <Footer />
        </div>
       </div>
    );

}

const AppContainer = props =>{

  

  return (<>
  <Switch>
  <Route  path="/Studchat" component={Anim}/>
  <Route  path="/" component={App}/>
  </Switch>
  <div className='Global_Switch'>
  <NavLink exact to='/Studchat'><img src={Game}/></NavLink>
  <NavLink exact to='/Home'><img src={All}/></NavLink>
  </div>

</>

  )
}

export default AppContainer;

